import React from 'react';
import { Button, SearchInput } from 'evergreen-ui';
import './LocationInput.css';

function LocationInput({
  setRestaurant,
  setShowRestaurantCard
}) {
  const onSearchClick = () => {
    const location = "Evanston";
    const restaurant = getRestaurant(location);
    setRestaurant(restaurant);
    setShowRestaurantCard(true);
  };

  const getRestaurant = (location) => {
    const API_URL = 
      `https://api.yelp.com/v3/businesses/search?location=${location}&term=food`;

    const API_KEY = 
      "yVcBeWfnp62Sv-z5C7KnmWss3uRRDJV1TZlHxIPwQO-EBEeOJD3r_mJT8bC_zKJlkvlEG7BDePiGRGLFpEmPWmb946UthWPZTNCNYrRtDeQ6wvP8jPEY-tagBR6sYHYx";
    
    fetch(API_URL, {
      headers: {
        "Access-Control-Allow-Origin": "*",
        "Authorization": `Bearer ${API_KEY}`
      }
    }).then(response => response.json())
      .then(data => console.log(data));

    return "Dave's Kitchen"
  }

  return (
    <div id="location-input">
      <SearchInput
        placeholder="First, what's your location?" />
      <Button
        marginLeft={12}
        onClick={onSearchClick}>
          Decide, please!
      </Button>
    </div>
  );
}

export default LocationInput;
